@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap");
.list {
  color: #fff;
  width: 300px;
  background-color: #f1d9d9;
  height: 50px;
  margin: 20px auto;
  border-radius: 5px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.5);
  font-family: "Roboto Slab", serif;
  font-size: larger;
}
.list p {
  padding: 10px;
  position: relative;

  color: rgb(20, 20, 44);
}
.list p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.faicons {
  margin: 0.5px;
  font-size: 16px;
}

.list p input {
  background-color: transparent;
  border: 0;
  font-size: inherit;
  outline: none;
}
