@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
.list {
  color: #fff;
  width: 300px;
  background-color: #f1d9d9;
  height: 50px;
  margin: 20px auto;
  border-radius: 5px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.5);
  font-family: "Roboto Slab", serif;
  font-size: larger;
}
.list p {
  padding: 10px;
  position: relative;

  color: rgb(20, 20, 44);
}
.list p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.faicons {
  margin: 0.5px;
  font-size: 16px;
}

.list p input {
  background-color: transparent;
  border: 0;
  font-size: inherit;
  outline: none;
}

body {
  background-color: #8f4ec5;
}
.App {
  background-color: rgb(90, 70, 141);
  min-height: 500px;
  width: 400px;
  margin: 100px auto;
  border-radius: 10px;
  box-shadow: 0 0 32px rgba(63, 49, 96, 1);
}
#to-do-form input {
  background: rgb(71, 54, 110);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
  box-shadow: 0 0 12px rgba(105, 105, 105, 0.5);
}
#to-do-form input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
#to-do-form input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
#to-do-form input::-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
#to-do-form input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

#to-do-form input:focus,
#to-do-form button:focus {
  outline: none;
}
#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgba(202, 156, 24);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.5);
}

